.kv_area {
  width: 100%;
  height: 400px;
  background: url(/index/img/kv.webp) center/cover;
  margin-bottom: 40px;
}
.kv_area .inner {
  width: 100%;
  height: 100%;
  max-width: 1080px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}
.kv_area .inner h1 {
  color: #fff;
  font-size: 30px;
  font-weight: bold;
}

.lead_area {
  width: 100%;
  margin-bottom: 60px;
}
.lead_area .inner {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.lead_area .inner h2 {
  font-size: 24px;
  color: #666;
}
.lead_area .inner p {
  font-size: 13px;
  color: #666;
}
.lead_area .inner a.btn {
  width: 170px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #75a8ea;
  padding: 0 10px 0 20px;
  color: #fff;
}
.lead_area .inner a.btn i {
  display: block;
  width: 10px;
  height: 15px;
  background: #fff;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}

.achievements_area {
  width: 100%;
  text-align: center;
  margin-bottom: 105px;
}
.achievements_area h2 {
  font-size: 34px;
  color: #666;
  margin-bottom: 40px;
}
.achievements_area ul {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}
.achievements_area ul li {
  height: 45px;
  border-top: 1px solid #d8d8d8;
  border-bottom: 1px solid #d8d8d8;
  border-left: 1px solid #d8d8d8;
}
.achievements_area ul li:last-child {
  border-right: 1px solid #d8d8d8;
}
.achievements_area ul li a {
  width: 100%;
  height: 100%;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #478ab2;
  background: #fff;
  transition: all 0.3s;
}
.achievements_area ul li a:hover {
  background: #478ab2;
  color: #fff;
}
.achievements_area .img {
  width: 100%;
  background: #f4f4f4;
}